.footer {
  position: relative;
  height: 30%;
}
.footer > h3 {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  font-size: 0px;
  white-space: nowrap;
  transition: font-size .3s ease;
}
.footer.active > h3 {
  font-size: 20px;
}
.coffee {
  height: 0px;
  margin: 0 3px;
  transition: height .3s ease;
}
.active > h3 > .coffee {
  height: 32px;
  width: 32px;
}
.footer > ul {
  list-style: none;
  font-size: 0px;
  padding: 0;
  text-align: center;
  white-space: nowrap;
  transition: font-size .3s ease;
}
.footer.active > ul {
  font-size: 17px;
}
.footer > ul > li > a {
  color: black;
  text-decoration: none;
}
.footer > ul > li > a:hover {
  border-bottom: 2px solid black
}
@media (min-width: 768px) {
  .footer > h3 {
    font-size: 20px
  }
  .footer > ul {
    font-size: 17px;
  }
  .coffee {
    height: 32px;
    width: 32px;
  }
}
